import React, { useState, useEffect, useCallback } from 'react'
import { calculator } from '../../utils'
import _ from 'lodash'
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts'
import { withTranslation } from 'react-i18next'

const CALCULATOR_MODE = 'cashOut'
const SimpleCashOutCalculator = ({ avgDay, avgTransaction, rates }) => {
  const [amount, setAmount] = useState(10000)
  const [currency, setCurrency] = useState('USD')
  const [length, setLength] = useState(365)
  const [cashOutPeriod, setCashOutPeriod] = useState('weekly')
  const [transactions, setTransactions] = useState(100)
  const [totalaccCashOutValue, setTotalaccCashOutValue] = useState(0)
  const [currentHODLCount, setCurrentHODLCount] = useState(0)
  const [avgCashOutValue, setAvgCashOutValue] = useState(0)
  const [chartData, setChartData] = useState([])
  const getResult = () => {
    const currentDate = new Date()
    if (rates > 0) {
      const result =
        calculator(
          amount,
          length,
          transactions,
          currentDate.valueOf(),
          CALCULATOR_MODE,
          null,
          rates,
          currency,
          cashOutPeriod
        ) || 0
      const totalaccCashOutValue = result.totalaccCashOutValue
      const avgCashOutValue = result.avgCashOutValue
      const currentHODLCount = result.currentHODLCount
      const chartData = result.chartData
      console.log('result', result)
      setChartData(chartData)
      setTotalaccCashOutValue(totalaccCashOutValue)
      setAvgCashOutValue(avgCashOutValue)
      setCurrentHODLCount(currentHODLCount)
    }
  }
  const callBackGetResult = useCallback(_.debounce(getResult, 1000), [
    amount,
    length,
    transactions,
    currency,
    cashOutPeriod
  ])
  useEffect(() => {
    callBackGetResult(amount, length, transactions, currency, cashOutPeriod)
  }, [amount, length, transactions, currency, cashOutPeriod])

  return (
    <>
      <div className="info-session">
        <form>
          <div>
            <>
              If I hold
              <input
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
              <select
                value={currency}
                onChange={(e) => setCurrency(e.target.value)}
              >
                <option value="USD">USD</option>
                <option value="HODLC">HODLC</option>
              </select>
              for
              <input
                type="number"
                min={1}
                value={length}
                onChange={(e) =>
                  setLength(e.target.value ? Number(e.target.value) : '')
                }
              />
              days,
              <br />
            </>
            and I assume
            <div className="ui input">
              <input
                type="number"
                value={transactions}
                onChange={(e) => setTransactions(e.target.value)}
              />
            </div>
            transactions take place every day. <br />
            <small className="notes">
              References: there are about <b>{avgTransaction}</b> transactions
              per day in the last <b>{avgDay}</b> days
            </small>
            <br />I plan to cash out
            <select
              value={cashOutPeriod}
              onChange={(e) => setCashOutPeriod(e.target.value)}
            >
              <option value="daily">daily</option>
              <option value="weekly">weekly</option>
              <option value="monthly">monthly</option>
            </select>
            .
          </div>
        </form>
        <div className="result-text">
          I can cash out on average
          <b>
            {' '}
            ${avgCashOutValue && avgCashOutValue.toFixed(2)}
          </b> <b>USD</b> {cashOutPeriod}.
        </div>
        <div className="result-text">
          I will be cashing out
          <b>
            {' '}
            ${totalaccCashOutValue && totalaccCashOutValue.toFixed(2)}
          </b>{' '}
          <b>USD</b> in total and my HODLC value will remain the same at the end
          of the period.
        </div>
        <div className="result-text">
          I will hold
          <b>
            {' '}
            {currentHODLCount && currentHODLCount.toFixed(2)}
          </b> <b>HODLC</b> at the end of the period.
        </div>
      </div>
      <div className="chart-container">
        <ResponsiveContainer>
          <LineChart
            data={chartData}
            margin={{
              top: 10,
              right: 20,
              left: 10,
              bottom: 10
            }}
          >
            <XAxis dataKey="timeLength" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              unit=" USD"
              dataKey="value"
              strokeWidth="3px"
              stroke="#3a2bac"
              dot={false}
              name="Value"
            />
            <Line
              type="monotone"
              unit=" USD"
              dataKey="growth"
              strokeWidth="0px"
              stroke="#008000"
              dot={false}
              activeDot={false}
              name="Growth"
              legendType="none"
            />
            <Line
              type="monotone"
              unit=" USD"
              dataKey="cashOutValue"
              strokeWidth="0px"
              stroke="#ef1552"
              dot={false}
              activeDot={false}
              name="Cash Out Value"
              legendType="none"
            />
            <Line
              type="monotone"
              unit=" USD"
              dataKey="accCashOutValue"
              strokeWidth="3px"
              stroke="#00bfc7"
              activeDot={{ r: 8 }}
              dot={false}
              name="Accumalated Cash Out Value"
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </>
  )
}

export default withTranslation()(SimpleCashOutCalculator)
