import React, { useState, useEffect, useCallback } from 'react'
import { calculator } from '../../utils'
import _ from 'lodash'
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts'
import { withTranslation } from 'react-i18next'
import BigNumber from 'bignumber.js'

const CALCULATOR_MODE = 'holding'
const SimpleCalculator = ({ avgDay, avgTransaction, rates }) => {
  const [amount, setAmount] = useState(10000)
  const [currency, setCurrency] = useState('USD')
  const [length, setLength] = useState(365)
  const [transactions, setTransactions] = useState(100)
  const [result, setResult] = useState(0)
  const [chartData, setChartData] = useState([])
  const [totalGrowth, setTotalGrowth] = useState('0')
  const getResult = () => {
    const currentDate = new Date()
    if (rates > 0) {
      const result =
        calculator(
          amount,
          length,
          transactions,
          currentDate.valueOf(),
          CALCULATOR_MODE,
          null,
          rates,
          currency
        ) || 0
      const endValueInUSD = result.totalValueAtTheEnd
      const chartData = result.chartData
      const totalGrowth = result.totalGrowth
      console.log('result', result)
      setChartData(chartData)
      setResult(endValueInUSD)
      setTotalGrowth(totalGrowth)
    }
  }
  const callBackGetResult = useCallback(_.debounce(getResult, 1000), [
    amount,
    length,
    transactions,
    currency
  ])
  useEffect(() => {
    callBackGetResult(amount, length, transactions, currency)
  }, [amount, length, transactions, currency])

  return (
    <>
      <div className="info-session">
        <form>
          <div>
            <>
              If I hold
              <input
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
              <select
                value={currency}
                onChange={(e) => setCurrency(e.target.value)}
              >
                <option value="USD">USD</option>
                <option value="HODLC">HODLC</option>
              </select>
              for
              <input
                type="number"
                min={1}
                value={length}
                onChange={(e) =>
                  setLength(e.target.value ? Number(e.target.value) : '')
                }
              />
              days,
              <br />
            </>
            and I assume
            <div className="ui input">
              <input
                type="number"
                value={transactions}
                onChange={(e) => setTransactions(e.target.value)}
              />
            </div>
            transactions take place every day. <br />
            <small className="notes">
              References: there are about <b>{avgTransaction}</b> transactions
              per day in the last <b>{avgDay}</b> days
            </small>
          </div>
        </form>
        <div className="result-text">
          The value is estimated to become{' '}
          <b>
            {result &&
              result.toLocaleString('en-US', {
                style: 'currency',
                currency: 'USD'
              })}
          </b>{' '}
          <b>USD</b> at the end of the period.
        </div>
        <div className="result-text">
          The total potential growth is{' '}
          <b>
            {totalGrowth &&
              totalGrowth.toLocaleString('en-US', {
                style: 'currency',
                currency: 'USD'
              })}
          </b>{' '}
          <b>USD</b> at the end of the period.
        </div>
        <div className="result-text">
          The potential percentage increase is{' '}
          <b>
            {totalGrowth &&
              amount &&
              BigNumber(totalGrowth).dividedBy(amount).times(100).toFixed(2)}
          </b>{' '}
          <b>%</b> at the end of the period.
        </div>
      </div>
      <div className="chart-container">
        <ResponsiveContainer>
          <LineChart
            data={chartData}
            margin={{
              top: 10,
              right: 20,
              left: 10,
              bottom: 10
            }}
          >
            <XAxis dataKey="timeLength" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              unit="USD"
              dataKey="value"
              strokeWidth="3px"
              stroke="#3a2bac"
              activeDot={{ r: 8 }}
              dot={false}
            />
            <Line
              type="monotone"
              unit="USD"
              dataKey="growth"
              strokeWidth="3px"
              stroke="#00bfc7"
              dot={false}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </>
  )
}

export default withTranslation()(SimpleCalculator)
